<template>
  <div class="m-activity">
    <div class="m-activity-top-bg"></div>

    <div class="m-activity-box">
      <div class="m-activity-item" v-for="item in activityList">
        <img class="m-activity-img" src="http://www.cssqt.com.cdn.suixin8.com/uploads/520_4961/201811/db05483b3a09487dea5a44f380194cad.jpg" alt="">
        <div class="m-activity-btn">{{item.btn}}</div>
        <!--<div class="m-activity-btn" :class="item.active?'active':''">首单可免</div>-->
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import common from '../../../common/js/common';

  export default {
    data() {
      return {
        name: '',
        activityList: [
          { record: "", btn: "首单可免", share: false },
          // { record: "", btn: "参与竞猜", share: false },
          // { record: "", btn: "邀请好友帮拆魔盒", share: false },
          // { record: "", btn: "我要试用", share: false }
        ]
      }
    },
    components: {},
    methods: {
      // 跳转页面
      changeRoute(v){
        this.$router.push(v)
      }
    },
    mounted() {
      common.changeTitle('活动');
    }
  }
</script>
<style lang="less" rel="stylesheet/less" scoped>
  @import "../../../common/css/index";

  .m-activity {
    .m-activity-top-bg {
      width: 750px;
      height: 350px;
      background: linear-gradient(180deg,rgba(252,211,22,1) 0%,rgba(239,232,197,1) 100%);
    }
    .m-activity-box {
      min-height: 100%;
      margin-bottom: 300px;
      .m-activity-item {
        width: 700px;
        height: 500px;
        margin-left: 25px;
        box-shadow: 0 5px 6px rgba(0,0,0,0.16);
        border-radius: 20px;
        position: relative;
        .m-activity-img {
          width: 700px;
          height: 500px;
          background: #ffffff;
          border-radius: 20px;
        }
        .m-activity-btn {
          width: 300px;
          color: #AF3300;
          font-size: 38px;
          font-weight: bold;
          padding: 20px 25px;
          background: linear-gradient(180deg,rgba(255,235,148,1) 0%,rgba(252,211,22,1) 100%);
          box-shadow: 0 5px 10px rgba(0,0,0,0.16);
          border-radius: 50px;
          position: absolute;
          bottom: 50px;
          left: 175px;
        }
      }
    }
  }
</style>



